const getDateMonthName = (dateIn) => {
  const numDate = new Date(dateIn);
  const monthNames = ['', '', '', '',
    '', '', '', '',
    '', '', '', ''];

  return `${numDate.getDate()} ${monthNames[numDate.getMonth()]} ${numDate.getFullYear()}`;
};

export default getDateMonthName;



